import React, { useState } from 'react';
import { DatePickerInput, Input, Label, LoginButton } from '..';
import * as S from './styled';

const ConfigForm = (props) => {
  const [quantidade, setQuantidade] = useState('');
  const [valor, setValor] = useState('');
  const [ date, setDate] = useState(new Date());

  const handleOnChangeVal = (e) => {
    setValor(e)
  };

  const handleOnChangeQuant = (e) => {
    setQuantidade(e)
  };

  const handleOnSubmit = () => {
    let passport = {
      value: valor,
      transactionAt: date,
      amount:quantidade
    };
    props.handleSubmit(passport);
  };

  const handleDateChange = (e) => {
    setDate(e)
  };

  return (
    <S.Container>
      <S.FormContainer>
        <S.FormGroup>
          <Label label="Data de Compra" />
          <DatePickerInput onChange={handleOnChangeVal} />
        </S.FormGroup>
        <S.FormGroup>
          <Label label="Quantidade" />
          <Input type="number" onChange={handleOnChangeQuant} marginBottom="0" />
        </S.FormGroup>
        <S.FormGroup>
          <Label label="Valor (R$)" />
          <Input type="number" onChange={handleDateChange} marginBottom="0" />
        </S.FormGroup>
      </S.FormContainer>
      <LoginButton handleClick={handleOnSubmit} label="Cadastrar" disabled={false} />
    </S.Container>
  );
};
export default ConfigForm;
